.category-tree-mask {
    position: fixed;
    top: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, .2);
    z-index: 1000;
    display: none
}

.category-tree-mask .category-tree-box {
    width: 85%;
    max-width: 570px;
    height: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 10px 0;
    background-color: #fff
}

.category-tree-mask .category-tree-box .category-tree {
    height: 100%;
}

.el-tree-node__label {
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    overflow: hidden;
}
